.actor-card {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vw;
    width: 15%;
    min-width: 150px;
    filter: grayscale(1);
}

.actor-card img{
    width: 100%;
}

.actor-card .overlay {
    max-height: 0;
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    transition: 300ms;
    overflow: hidden;
} 

.actor-card:hover .overlay {
    max-height: 500px;
    padding: 15px;
}

.actor-card:hover {
    transform: scale(1.05);
    filter: grayscale(0);
    transition: 300ms;
}


.actor-card .no-name {
    text-align: center;
    max-height: 500px;
    color: white;
} 