.movie-card {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2vw;
    width: 15%;
    min-width: 150px;
}

.movie-card img{
    width: 100%;
}

.movie-card .overlay {
    max-height: 0;
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    margin: 0;
    transition: 300ms;
    overflow: hidden;
}

.movie-card .overlay {
    padding: 10px;
}

.movie-card:hover .overlay {
    max-height: 200px;
}

.movie-card:hover {
    transform: scale(1.1);
    transition: 300ms;
}

.cards-showmore {
    display: block;
    width: 50%;
    color: white;
    padding: 15px;
    box-sizing: border-box;
    background-color: var(--primary-bg);
    margin: auto;
    border-radius: 5px;
    border: none;
    font-family: 'Lobster', cursive;
    font-size: 1.5em;
    letter-spacing: 4px;
    transition: background-color 600ms;
    border: 1px solid var(--primary-bg);
}

.cards-showmore:hover {
    color: white;
    background-color: black;
    border: 1px solid var(--primary-bg);
}