.header-trend-movie {
    position: relative;
    width: 100%;
    max-height: calc(100vh - 80px);
    overflow: hidden;
}
.header-trend-movie img {
    width: 100%;
}

.badge {
    position: absolute;
    top: 7px;
    left: -32px;
    transform: rotate(-45deg);
    background-color: var(--primary-bg);
    color: white;
    font-weight: bold;
    z-index: 2;
    padding: 5px 35px;
    box-shadow: 5px 3px 15px black;
}

.trend-movie-overview {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 0 15px;
    border-radius: 5px;
    font-size: 1.7em;
}

.trend-movie-overview .title { 
    font-family: 'Lobster', cursive;
}

.trend-movie-overview .txt { 
    text-align: justify;
}

.links {
    display: flex;
    justify-content: center;
    padding: 10px;
}

.links a {
    color: white;
    text-decoration: none;
    border: 1px solid white;
    border-radius: 5px;
    padding: 8px 15px;
    transition: 90ms;
}

.links a:hover {
    color: white;
    background-color: var(--primary-bg);
}

@media screen and (max-width: 585px) {
    .trend-movie-overview h2, .trend-movie-overview .txt{
        padding: 0;
    }
    .trend-movie-overview h2 {
        text-align: center;
    }

    .trend-movie-overview {
        left: 0;
        transform: translate(0, -50%);
    }
}


@media screen and (max-width: 470px) {
    .trend-movie-overview {
        font-size: 0.9em;
    }

    .badge {
        display: none;
    }

    .links {
        margin: 0;
        margin-bottom: 5px;
        padding: 0;
    }
    .trend-movie-overview .title {
        margin: 5px;
    }

    .links a {
        padding: 3px 7px;
    }
}

@media screen and (max-width: 360px) {
    .trend-movie-overview {
        font-size: 0.75em;
    }

}

/* ************************************
    Search bar
*************************************** */
.search-bar {
    display: flex;
    background-color: var(--primary-bg);
    align-items: center;
    justify-content: center;
    padding: 15px;
    overflow: hidden;
}

.search-bar input {
    width: 30%;
    height: 100%;
    height: 15px;
    padding: 7px 15px;
    border: none;
    outline: none;
    border-radius: 15px 0 0 15px;
}

.search-icon-container {
    /* position: relative;
    left: -30px; */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border: none;
    outline: none;
    border-radius: 0 15px 15px 0;
    height: 29px;
    width: 35px;
}

.search-icon-container:hover {
    background-color: var(--primary-font);
}

.search-icon-container i {
    color: var(--primary-font);
    font-size: 1.1em;
}

.search-icon-container:hover i {
    color: white;
}