.movie-detail-container{
    background-color: black;
    box-sizing: border-box;
    opacity: 0.9;
}

.bg-image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    filter: blur(7px);
    z-index: -1;
}