.movies-list {
    background-color: #1d1d1d;
    height: 100vh;
    /* flex: 1; */
    width: 30%;
}

.movie-list-title {
    position: relative;
    background-color: #901818;
}

.movie-list-title h2{
    color: white;
    text-align: center;
    margin: 0;
    padding: 20px 0;
    font-family: 'Lobster', cursive;
    letter-spacing: 2px;
}

.list {
    color: white;
    font-weight: bold;
}


@media screen and (max-width: 900px) {
    .movies-list {
        background-color: #1d1d1d;
        height: 10vh;
        width: 100%;
        overflow-y: scroll;
    }

    .movies-list h2 {
        padding: 0 7px;
    }
    
    .list {
        font-weight: normal;
    }

    .list-item {
        padding: 10px;
    }
}