.flex-container { 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}

.card {
    display: flex;
    flex-direction: column;
    width: 333px;
    height: 85vh;
    background-color: #1d1d1d;
    color: white;
    border-radius: 8px 8px 0 0;
    overflow: hidden;
    /* box-shadow: 0px 0px 8px black; */
    box-shadow: 0px 0px 12px rgb(51, 51, 51);
    font-size: 1.5rem;
}

.card .movie-img {
    width: 100%;
}

.card .movie-img img {
    width: 100%;
    height: auto
}

.movie-description {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: calc(100vh - 80px);
    color: black;

}

.movie-description * {
    margin: 0;
    padding: 0;
}

.movie-detail {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0 10px;
    height: 50%;
}

.movie-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

h2 {
    font-family: 'Roboto Mono', monospace;
    font-size: 1.2em;
}

.movie-title h2 {
    font-family: 'Lobster', cursive;
    font-size: 2em;
}

.movie-stars {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.stars {
    display: flex;
    align-items: center;
}

.stars-txt, .movie-genre p {
    padding-right: 5px;
}

.actors a {
    color: white;
    text-decoration: none;
}

.actors:hover a{
    text-decoration: underline;
}

.movie-genre {
    display: flex;
    /* padding: 0 10px; */
}

.movie-genre ul {
    display: flex;
    list-style-type: none;
    /* padding: 0 10px; */
}

.movie-genre ul li:not(:nth-child(1))::before {
    content: ', '; 
}

.movies-links {
    display: flex;
    justify-content: space-between;
}
.movies-links i {
    font-size: 1.2em;
}

.movies-links p{
    text-align: center;
    flex: 1;
    padding: 10px;
    transition: 150ms;
}

.movies-links p:hover {
    color: #1d1d1d;
    background-color: #fff;
}


@media all and (max-height: 900px) {
    .card {
        width: 300px;
        height: 95vh;
    }
    
}

@media all and (max-height: 750px) {
    body {
        font-size: 1.2rem;
    }

    .card {
        width: 250px;
        height: 95vh;
    }
    
}


@media all and (max-height: 750px) {
    body {
        font-size: 1.2rem;
    }

    .card {
        width: 250px;
        height: 95vh;
    }
    
}


@media all and (max-height: 700px ) and (min-width: 650px) {
    body {
        font-size: 1.4rem;
    }

    .card {
        flex-direction: row;
        width: 500px;
        height: auto;
        justify-content: space-evenly;
        align-items: center;
    }

    .movie-detail {
        display: flex;
        flex-direction: column;
    }

    .movie-detail .movie-title {
        padding: 7px 0;
        font-size: 1.2em;
    }
    .movie-detail .movie-overview{
        padding: 7px 0;
    }
    .movie-detail .movie-stars{
        padding: 7px 0;
    }
}