header {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: var(--primary-bg);
}

.header-banner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--primary-bg);
    color: var(--primary-font);
    padding: 15px 15px;
    z-index: 3;
}

.header-banner h1 .header-txt {
    font-family: 'Bebas Neue', monospace, cursive;
    letter-spacing: 8px;
    color: var(--primary-font);
    text-decoration: none;
    font-size: 3.2rem;
}

.header-bookmark {
    color: var(--primary-font);
    margin-right: 2rem;
    cursor: pointer;
}

.header-badge {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    border-radius: 1.3rem;
    background: white;
    color: #1d1d1d;
    right: 2rem;
    bottom: 1rem;
    font-weight: bolder;
    font-size: 1.2rem;
}