
/* ************************************
    Search bar
*************************************** */
.search-bar {
    position: relative;
    display: flex;
    background-color: var(--primary-bg);
    align-items: center;
    justify-content: center;
    padding: 20px;
    overflow: hidden;
    top: -5px;
}

.search-bar input {
    width: 30%;
    height: 100%;
    padding: 7px 15px;
    border: none;
    outline: none;
    border-radius: 15px 0 0 15px;
}

.search-icon-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border: none;
    outline: none;
    border-radius: 0 15px 15px 0;
    height: 29px;
    width: 35px;
}

.search-icon-container:hover {
    background-color: var(--primary-font);
}

.search-icon-container .searchIcon {
    color: var(--primary-font);
    font-size: 1.3em;
}

.search-icon-container:hover  .searchIcon {
    color: white;
}