.load-btn {
    display: block;
    width: 50%;
    color: white;
    padding: 15px;
    box-sizing: border-box;
    background-color: var(--primary-bg);
    margin: auto;
    border-radius: 5px;
    border: none;
    font-family: 'Lobster', cursive;
    font-size: 1.5em;
    letter-spacing: 4px;
    transition: background-color 600ms;
    border: 1px solid var(--primary-bg);
}

.load-btn-text {
    text-align: center;
}

.load-btn:hover {
    color: white;
    background-color: black;
    border: 1px solid var(--primary-bg);
}