* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary-bg: #1d1d1d;
  --primary-font: #d7171e;
  --secondary-font: white;
}


html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
}

div {
  overflow: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.fa-faSpinner {
  color: #a81818;
  margin: 10rem auto;
}
